@import url('https://fonts.googleapis.com/css2?family=League+Spartan:wght@400;700&family=Poppins:wght@400;700&family=Roboto:wght@400;700&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    min-height: 100vh;
    font-family: "Poppins", sans-serif;
}

.container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.title{
    font-size: 3rem;
    margin: 6vh;
    padding: 1vh 5vw;
    border: 7px dashed rgb(1,7,160);
    border-radius: 20px;
}
.cards{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    max-width: 1300px;
    margin: 0 auto;

}
.card{
    width: 400px;
    height: max-content;
    margin: 1rem;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    align-items: center;
    box-shadow: rgba(0,0,0,0.24) 0px 3px 8px;
}

.image{
    width: 380px;
    aspect-ratio: 1/1;
    object-fit: cover;
}
.tour-info{
    margin: 20px 5px;
    gap: 5px;
}
.tour-price{
    color:#1faa59;
    font-size: 1.3rem;
}
.tour-name{
    font-size: 1.5rem;
}
.read-more{
    cursor: pointer;
    text-transform: capitalize;
    color: #12b0e8;
}
.btn-red{
    margin-top: 18px;
    padding: 10px 80px;
    border: 1px solid #b4161b;
    cursor: pointer;
    border-radius: 10px;
    font-size: 18px;
    font-weight: bold;
    background-color: #b4161b21;
    
}
.btn-red:hover{
    color: white;
    background: #b4161b;
    transition: all 0.5s;
}

.refresh{
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.refresh h3{
    font-size: 2rem;
    margin-bottom: 20px;
}
.btn-refresh{
    cursor: pointer;
    background-color: white;
    border: none;
}
.refreshImg{
    width: 40px;
}